import { Component }						from '@angular/core';
import { DomSanitizer }                 	from '@angular/platform-browser';
import { NavParams, LoadingController }   	from 'ionic-angular';
import { C8oRouter }                    	from 'c8ocaf';
import { C8oPage }                      	from 'c8ocaf';
import {ChangeDetectorRef, ChangeDetectionStrategy, InjectionToken, Injector, Type} from "@angular/core";

/*
	You can customize your page class by writing code between the :

   		Begin_c8o_XXXX and
   		End_c8o_XXXX
   		
   	Comments.
   	
   	Any code placed outside these these comments will be lost when the application is generated
*/

     
/*Begin_c8o_PageImport*/
/*End_c8o_PageImport*/

@Component({
  selector: 'page-details',
  templateUrl: 'details.html',
})
export class DETAILS extends C8oPage  {
	
	
	/*Begin_c8o_PageDeclaration*/
    TLine = 0;
    TTline = [80,82,83,85,88,90,95,85,82,80,75,65,60,50,40,38,39,40,40,40,39,38,38,40];
//    TTline = [80,60,50,40];
    idx = 0;
	/*End_c8o_PageDeclaration*/
	
	constructor(routerProvider : C8oRouter, navParams: NavParams, loadingCtrl: LoadingController, sanitizer: DomSanitizer, ref: ChangeDetectorRef, injector: Injector){
		super(routerProvider, navParams, loadingCtrl, sanitizer, ref, injector);
		
		
		
		/*Begin_c8o_PageConstructor*/
		/*End_c8o_PageConstructor*/
		
    }
	
	/*Begin_c8o_PageFunction*/
	
	ionViewDidLoad(){
	    this.changeTLine();
	}
	
	changeTLine() : void {
	    if(this.idx < this.TTline.length){
	        this.TLine = this.TTline[this.idx++];
	        window.setTimeout(this.changeTLine.bind(this), 750);
	    }else{
	        this.TLine = this.TTline[this.TTline.length-1];
	    }
	}
	/*End_c8o_PageFunction*/
	
	
}



